import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import alebymars from '../img/alebymars.png';
import './Alebymars.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Alebymars = props => (
	<Panel id={props.id}>
		<PanelHeader
			addon={<HeaderButton onClick={props.go} data-to="home">Назад</HeaderButton>}
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Крестики - Нолики
		</PanelHeader>
		<iframe className="Alebymars" src="https://ceofficial.github.io" width="350" height="400" scrolling="no" frameborder="0">
    Ваш телефон не поддерживает данное приложение!
 </iframe>

	</Panel>
);

Alebymars.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


export default Alebymars;



